  background-color: #ffffff;
  width: 2em;
  line-height: 2em;
  text-align: center;
  padding: 0;
  color: #848484;
}
.css-scroll-below .css-scroll-below-clear-icon {
  color: #848484;
}
.css-scroll-below.css-scroll-below-clearable .css-scroll-below-label {
  padding-right: 4em;
}
.css-scroll-below-panel {
  padding: 0;
  border: 1px solid #c8c8c8;
  background-color: #ffffff;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
}
.css-scroll-below-panel .css-scroll-below-filter-container {
  padding: 0.429em 0.857em 0.429em 0.857em;
  border-bottom: 1px solid #eaeaea;
  color: #333333;
  background-color: #ffffff;
  margin: 0;
}
.css-scroll-below-panel .css-scroll-below-filter-container .css-scroll-below-filter {
  width: 100%;
  padding-right: 2em;
}
.css-scroll-below-panel .css-scroll-below-filter-container .css-scroll-below-filter-icon {
  top: 50%;
  margin-top: -0.5em;
  right: 1.357em;
  color: #007ad9;
}
.css-scroll-below-panel .css-scroll-below-items {
  padding: 0;
}
.css-scroll-below-panel .css-scroll-below-items .css-scroll-below-item,
.css-scroll-below-panel .css-scroll-below-items .css-scroll-below-item-group {
  margin: 0;
  padding: 0.429em 0.857em;
  border: 0 none;
  color: #333333;
  background-color: transparent;
  border-radius: 0;
}
.css-scroll-below-panel .css-scroll-below-items .css-scroll-below-item.ui-state-highlight,
.css-scroll-below-panel
  .css-scroll-below-items
  .css-scroll-below-item-group.ui-state-highlight {
  color: #ffffff;
  background-color: #007ad9;
}
.css-scroll-below-panel
  .css-scroll-below-items
  .css-scroll-below-item:not(.ui-state-highlight):not(.ui-state-disabled):hover,
.css-scroll-below-panel
  .css-scroll-below-items
  .css-scroll-below-item-group:not(.ui-state-highlight):not(.ui-state-disabled):hover {
  color: #333333;
  background-color: #eaeaea;
}

